.switch {
  position: relative;
  display: inline-block;
  width: 71px;
  height: 34px;
  display: flex;
}

/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--text-color-light);
  -webkit-transition: .4s;
  transition: .4s;
  text-align: right;
  font-size: var(--smaller-font-size);
  line-height: 34px;
  span {
    position: absolute;
    display: block;
    width: fit-content;
    height: 100%;
    right: 10px;
    left: initial;
    z-index: 1;
    color: #FFF;
  }
}

.slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 6px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  z-index: 2;
}

input:checked + .slider {
  background-color: var(--first-color);
  span{
    position: absolute;
    left: 10px;
    right: 0;
  }
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--first-color);
}

input:checked + .slider:before {
  left: 44px;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}