.menu__item {
  margin-bottom: var(--mb-1-5);
  width: 100%;
  position: relative;
  &:last-child {
    margin-bottom: 0;
  }
  &-link {
    display: flex;
    font-size: var(--h3-font-size);
    color: black;
    font-weight: var(--font-medium);
    border-radius: 50px;
    width: 100%;
  }
  &-card {
    display: flex;
    flex-direction: column;
    background: linear-gradient(
      180deg
      ,var(--first-color), var(--first-color-lighter) 45%,#fff 0,#fff);
    transition: .4s ease-in-out;
    &:hover {
      img {
        transform: scale(1.1);
      }
    }
    img {
      width: 170px;
      height: 170px;
      border-radius: 50%;
      margin-bottom: 15px;
      border: 10px solid white;
      transition: .4s ease-in-out;
    }
  }
}