.sidebar {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100%;
  z-index: var(--z-fixed);
  background-color: rgba(0, 0, 0, 0.2);
  opacity: 0;
  transition: all .3s ease-in-out;
  overflow-y: unset;

  &__inner {
    max-width: 375px;
    background: var(--gradient);
    padding: 60px 60px 20px 37px;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    width: 100%;
    box-shadow: 3px 0px 10px rgba(0, 0, 0, 0.4);
    overflow-y: auto;
    position: relative;
    left: -100%;
    transition: all .3s ease-in-out;
    height: 100%;

    &::-webkit-scrollbar {
      background-color: var(--gradient);
    }
    @media screen and (max-width: 500px) {
      max-width: 80vw;
      padding: 10px;
    }
  }

  & *{
    color: white;
  }

  & .menu__list {
    margin-top: 50px;
    padding-left: 15px;
    @media screen and (max-width: 500px) {
      padding-left: 0;
    }
    .menu__item {
      margin-bottom: 5px;
    }
    .menu__item-link {
      width: fit-content;
      display: block;
      position: relative;
      padding: 10px;
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0px;
        width: 30px;
        height: 30px;
        background-color: var(--first-color-lighter);
        border-radius: 50%;
        transition: right .3s ease-in-out, opacity .3s ease-in-out;
        opacity: 0;
      }
      &:hover {
        &::after {
          right: -60px;
          opacity: 1;
        }
      }
      &.active {
        background: var(--first-color-lighter);
        &:hover{
          &::after {
            display: none;
          }
        }
      }
    }
  }

  &.show {
    display: flex;
    opacity: 1;
    .sidebar__inner {
      left: 0;
    }
  }
  
  &.hidden {
    visibility: hidden;
  }

  &__btn {
    font-size: var(--h2-font-size);
  }
}