.statistic-table__block {
  max-width: 100%;
  overflow: auto;
  margin: 0 auto;
  .row {
    margin-bottom: 15px;
  }
  table {
    width: 100%;
    min-width: 830px;
    border-collapse: collapse;
  
    td, th {
      padding: 5px 10px;
    }
    td {
      text-align: center;
    }
    tr {
      td {
        border-bottom: 1px solid var(--first-color);
      }
    }
  }
}
