.btn {
  padding: 5px 15px;
  background-color: black;
  border-radius: 50px;
  color: #FFF;
  border: none;
  outline: none;
  transition: .3s ease-in-out;
  cursor: pointer;
  font-size: initial;
  line-height: 100%;
  &:hover {
    background-color: rgb(87, 87, 87);
  }
  &--primary {
    height: 100%;
    padding: 10px 20px;
    border: none;
    outline: none;
    background-color: var(--first-color);
    color: #FFF;
    border-radius: 50px;
    margin: 0 5px;
    cursor: pointer;
    transition: background .3s ease-in-out;
    display: flex;
    align-items: center;
    font-size: initial;
    &:hover {
      background-color: var(--first-color-lighter);
    }
  }
  &--sidebar {
    background-color: transparent;
    border: 1px solid #FFF;
    border-radius: 4px;
    width: 100%;
    margin-top: 20px;
    &:hover {
      background-color: rgba(167, 167, 167, 0.288);
    }
  }
  &--round {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: var(--z-fixed);
    background-color: var(--first-color);
    padding: 10px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    cursor: pointer;
    color: #FFF;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    border: none;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.15);
    &:hover:not(:disabled) {
      transform: scale(1.1);
      background-color: var(--first-color);
    }
    &:disabled {
      background: gray;
      cursor: auto;
    }
    img{
      width: auto;
      height: 100%;
      object-fit: contain;
      padding: 5px;
    }
  }
  &--danger {
    background-color: red;
    &:hover {
      background-color: rgb(175, 47, 47);
    }
  }
}