.word {
  background-color: transparent;
  width: 100%;
  height: 268px;
  perspective: 1000px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  &[aria-disabled=true] {
    pointer-events: none;
    opacity: 0.7;
  }

  &:hover > &__card {
    transform: translateZ(2rem);
  }

  &__card {
    width: 100%;
    height: 100%;
    position: relative;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }

  &.flipped > &__card{
    transform: rotateY(-180deg);
  }

  &__front, &__back{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__front{
    background-color: white;
    flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    cursor: pointer;
    &-header {
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
    &-footer {
      width: 100%;
      min-height: 50px;
      border-top: 2px solid var(--first-color);
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      span {
        font-size: var(--h2-font-size);
      }
      .btn {
        padding: 10px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        color: white;
      }
    }

  }
  &__name {
    display: block;
    font-size: var(--small-font-size);
  }
  &__back{
    background-color: var(--first-color);
    background-position: center;
    background-size: cover;
    transform: rotateY(180deg);
    color: #FFF;
    font-size: var(--h2-font-size);
  }
}
