.auth {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  &__form {
    flex: 1 0 auto;
    padding: 15px 60px;
    padding-bottom: 90px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    input {
      width: 100%;
      padding: 4px;
      border: none;
      border-bottom: 2px solid var(--text-color);
      outline: none;
      font-size: var(--h2-font-size);
    }
  }

  &__footer {
    padding: 20px;
    display: flex;
    justify-content: center;
    background-color: var(--first-color);
    .btn {
      border-radius: 3px;
      border: 1px solid #FFF;
      padding: 5px 30px;
      font-size: var(--h2-font-size);
    }
  }
}