.header {
  .nav{
    padding-top: 48px;
    padding-bottom: 48px;
    display: flex;
    justify-content: space-between;
    &__toggle {
      position: relative;
      width: 45px;
      height: 30px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      cursor: pointer;
      z-index: 10000;
      &.close {
        position: fixed;
        span {
          background-color: white;
          opacity: 0;
          &:first-child {
            transform: rotate(45deg) translate(9px, 9px);
            opacity: 1;
          }
          &:last-child {
            transform: rotate(-45deg) translate(9px, -9px);
            opacity: 1;
          }
        }
      }
      &-item {
        width: 100%;
        height: 5px;
        background-color: var(--first-color);
        transition: .3s ease-in-out;
      }
    }
    &__btn {
      height: 100%;
      padding: 0 15px;
    }
    &__btns {
      display: flex;
      align-items: center;
      margin-left: auto;
      .nav__btns-start {
        bottom: -100%;
        animation: showStartButton 0.3s forwards;
      }
    }
  }
}

@keyframes showStartButton {
  0% {
    bottom: -100%;
  }

  100% {
    bottom: 20px;
  }
}