@import 'variables';
@import 'animations';

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  min-height: 100vh;
}

h1,
h2,
h3,
h4 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.container {
  max-width: 1008px;
  margin: 0 auto;
  padding-left: var(--mb-1-5);
  padding-right: var(--mb-1-5);
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 28px;
  margin-bottom: 28px;
  @media screen and (max-width: 768px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 500px) {
    grid-template-columns: repeat(1, 1fr);
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  & > * {
    margin: 5px;
  }
  &--flex-end {
    justify-content: flex-end;
  }
}


.result {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  img {
    max-width: 400px;
    max-height: 400px;
    object-fit: contain;
  }
  span {
    font-size: var(--h2-font-size);
    display: block;
    padding: 10px 15px;
    border-radius: 50px;
    background-color: var(--first-color);
    color: #FFF;
    text-align: center;
    width: fit-content;
    margin: 0 auto;
    margin-top: 30px;
    position: relative;
    &::after, &::before {
      position: absolute;
      content: '';
      top: 50%;
      transform: translateY(-50%);
      width: 35px;
      height: 35px;
      border-radius: 50%;
      background-color: var(--first-color);
    }
    &::after {
      right: -40%;
    }
    &::before {
      left: -40%;
    }
  }
}