.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.212);
  z-index: 10000;
  &.show {
    display: block;
    .modal__inner {
      opacity: 1;
      top: 50%;
    }
  }
  &.hidden { 
    visibility: hidden;
    .modal__inner {
      opacity: 0;
      top: 60%;
    }
  }
  &__inner {
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 612px;
    max-height: 430px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #FFF;
    box-shadow: 0px 4px 4px #00000025;
    opacity: 0;
    transition: all .3s ease-in-out;
  }
  &__header {
    padding: 10px;
    text-align: center;
    font-size: var(--h1-font-size);
  }
  &__body {
    height: 100%;
  }
  &__close {
    position: absolute;
    transform: translate(50%, -50%);
    right: 0;
    width: 40px;
    height: 40px;
    background-color: var(--first-color);
    border-radius: 50%;
    display: block;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 8px;
    cursor: pointer;
    transition: .3s ease-in-out;
    &:hover {
      transform: translate(50%, -50%) scale(1.1);
    }
    span{
      background-color: white;
      width: 100%;
      height: 2px;
      display: block;
      margin-left: 2px;
      &:first-child {
        transform: rotate(-45deg) translate(-1px);
      }
      &:last-child {
        transform: rotate(45deg) translate(-1px);
      }
    }
  }
}
